const MAP_KEY = 'f4d562092c8dbb27c9fed98afc99703b'


export function initMap() {
    return new Promise(resolve => {
        if(document.querySelector('#gaodeMap')) return resolve(window.AMap)
        window.onLoad = function () {
            resolve(window.AMap)
        }
        var url = `https://webapi.amap.com/maps?v=1.4.15&key=${MAP_KEY}&callback=onLoad`;
        var jsapi = document.createElement('script');
        jsapi.charset = 'utf-8';
        jsapi.src = url;
        document.head.appendChild(jsapi);
    })


}